html {
  -webkit-text-size-adjust: 100%; /* Prevent font scaling in landscape while allowing user zoom */
}

.App {
  text-align: center;
  max-width: 500px;
  margin: 0 auto;
  padding: 0;
}

h1 {
  text-transform: uppercase;
  border-bottom: 1px solid black;
  letter-spacing: 0.2rem;
  font-weight: 700;
  font-size: 36px;
  margin: 0 auto;
  padding: 0;
}

header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  background-color: #fff;
  padding: 15px 3px;
}

footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  padding-top: 10px;
}

header > *,
footer > * {
  max-width: 500px;
  margin: 0 auto;
}

code {
  font-size: 0.7em;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  margin: 0;
  max-width: initial;
}

button {
  border-radius: 5px;
  font-weight: bold;
  font-size: 1.2em;
  cursor: pointer;
  user-select: none;
  background-color: #d3d6da;
  color: #1a1a1b;
  border: 0;
  margin: 0 1px;
  flex: 1;
}

#gameboard {
  margin: 130px auto 150px;
}

#keyboard {
  padding: 0 5px;
}

#keyboard button {
  height: 50px;
  padding: 0;
  font-size: 1em;
}

.game-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 6px;
}

.tile {
  border: 2px solid #d3d6da;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  width: 50px;
  height: 50px;
  margin: 0 3px;
  font-size: 2rem;
  font-weight: bold;
  box-sizing: border-box;
}

.bull {
  background-color: #6aaa64;
  border: 0;
  color: #fff;
}

.cow {
  background-color: #c9b458;
  border: 0;
  color: #fff;
}

.none {
  background-color: #787c7e;
  border: 0;
  color: #fff;
}

#keyboard {
  display: flex;
  flex-direction: row;
}

#disclaimers {
  font-size: 12px;
  margin: 15px auto;
}

#overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

#eog-dialog {
  background-color: #fff;
  margin: 0 auto;
  width: 90%;
  max-width: 400px;
  border-radius: 10px;
  padding: 20px;
}

#eog-dialog button {
  padding: 10px;
}
